import * as React from 'react';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import './Comments.css';
import EmojiPicker from 'emoji-picker-react';
import { format } from 'timeago.js';
import instance from '../../api/axios';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 1100,
  bgcolor: 'background.paper',
  boxShadow: 24,
  padding: 0,
  borderRadius: 6,
};

function Comments({ commentOpen, setCommentOpen }) {
  const [emojiVisiblity, setEmojiVisiblity] = React.useState();
  const [commentInput, setCommentInput] = React.useState();
  const [comments, setComments] = React.useState([]);
  const [replyTag, setReplyTag] = React.useState(false);
  const [replyShow, setReplyShow] = React.useState(true);

  function showReply(id, show) {
    const current = document.getElementById(id);
    if (show) {
      current.classList.remove('hide-reply');
      setReplyShow(false);
    } else {
      current.classList.add('hide-reply');
      setReplyShow(true);
    }
  }

  const handleClose = () => setCommentOpen(false);

  const onEmojiClick = (e) => {
    setCommentInput(commentInput + e.emoji);
  };

  const likeComment = (id, isLiked) => {
    if (isLiked) {
      instance.post('/disLikeComment', { id }).then(() => {
        instance
          .post('/getComments', { id: commentOpen._id })
          .then(({ data }) => {
            setComments([...data]);
          });
      });
    } else {
      instance.post('/likeComment', { id }).then(() => {
        instance
          .post('/getComments', { id: commentOpen._id })
          .then(({ data }) => {
            setComments([...data]);
          });
      });
    }
  };

  const doComment = (id) => {
    if (replyTag) {
      instance
        .post('/replyComment', { id: replyTag._id, comment: commentInput })
        .then(() => {
          setCommentInput('');
          setReplyTag(false);
          instance
            .post('/getComments', { id: commentOpen._id })
            .then(({ data }) => {
              setComments([...data]);
            });
        });
    } else {
      instance.post('/commentPost', { id, comment: commentInput }).then(() => {
        setCommentInput('');
        instance
          .post('/getComments', { id: commentOpen._id })
          .then(({ data }) => {
            setComments([...data]);
          });
      });
    }
  };

  React.useEffect(() => {
    instance.post('/getComments', { id: commentOpen._id }).then(({ data }) => {
      setComments([...data]);
    });
  }, []);
  return (
    <Modal
      open={commentOpen}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
      disableAutoFocus
    >
      <Box sx={style}>
        <div className="comment-container">
          <div className="showPost-div">
            <img src={commentOpen.imageUrl} alt="" />
          </div>
          <div className="comment-div">
            <div className="comment-header">
              {/* <i className=""></i> */}
              <i
                onClick={() => setCommentOpen(false)}
                className="fa-regular fa-arrow-left back"
              />
              <span className="comment-title">Comments</span>
            </div>

            <div className="comment-main">
              {comments.length === 0 ? <div className="text-center text-xl"><h1>No Comments</h1></div> : comments.map((element) => (
                <div className="comment-section">
                  <div>
                    <div className="comment-img">
                      <img
                        src="data:image/png;base64,"
                        alt=""
                      />
                    </div>

                    <div className="comment-body">
                      <span className="user-name">
                        {element.userId.username}
                      </span>
                      <span>
                        {' '}
                        {element.comment}
                      </span>
                      <br />
                      <div className="muted">
                        <span>{format(element.createdAt)}</span>
                        <span>
                          <b>
                            {element.likes.length}
                            {' '}
                            Like
                          </b>
                        </span>
                        <span onClick={() => setReplyTag(element)}>
                          <b
                            onClick={() => {
                              replyShow
                                ? showReply(element._id, true)
                                : showReply(element._id, false);
                            }}
                          >
                            Reply
                          </b>
                        </span>
                        <span>
                          <i
                            onClick={() => likeComment(element._id, element.liked)}
                            className={
                                element.liked
                                  ? 'fa-heart fa-solid'
                                  : 'fa-heart fa-regular'
                              }
                          />
                        </span>
                      </div>
                    </div>
                  </div>

                  {/* Replays */}

                  <div id={element._id} className="hide-reply">
                    {element.replys.map((reply) => (
                      <div className="reply">
                        <div>
                          <div className="comment-img">
                            <img
                              src="data:image/png;base64,"
                              alt=""
                            />
                          </div>

                          <div className="comment-body">
                            <span className="user-name">
                              {reply.userId.username}
                            </span>
                            <span>
                              {' '}
                              {reply.comment}
                            </span>
                            <br />
                            <div className="muted">
                              <span>{format(reply.date)}</span>
                              {/* <span>
                                  <b>1 Like</b>
                                </span>
                                <span>
                                  <b>Reply</b>
                                </span>
                                <span>
                                  <i className="fa-regular fa-heart heart"></i>
                                </span> */}
                            </div>
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              ))}
            </div>
            <div className="comment-footer">
              <i
                onClick={() => setEmojiVisiblity(!emojiVisiblity)}
                className="fa-solid fa-face-smile emoji-icon"
              />

              {replyTag ? (
                <div className="reply-comment">
                  <div className="reply-detials">
                    <span>
                      <b>
                        @
                        {replyTag.userId.username}
                      </b>
                    </span>

                    <span className="reply-comment-content">
                      {replyTag.comment}
                    </span>
                  </div>

                  <div>
                    <i
                      onClick={() => setReplyTag(false)}
                      className="fa-regular fa-xmark reply-remove-icon"
                    />
                  </div>
                </div>
              ) : null}
              <input
                type="text"
                value={commentInput}
                onChange={(e) => setCommentInput(e.target.value)}
                placeholder={replyTag ? 'Add a reply...' : 'Add a comment...'}
              />
              <span
                onClick={() => doComment(commentOpen._id)}
                className="fa-sharp fa-solid fa-paper-plane sendCommentBtn"
              />
              <div className={`emoji ${emojiVisiblity ? null : 'hideImoji'}`}>
                <EmojiPicker width={450} onEmojiClick={onEmojiClick} />
              </div>
            </div>
          </div>
        </div>
      </Box>
    </Modal>
  );
}

export default Comments;
